@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
#app{
  text-align: center;
  
     font-family: 'Roboto';
     letter-spacing: 0;
}

.main{
  position: relative;
  width: 100%;
  color: #4e504f;
}
.main__block{
  margin-bottom: 150px;
}
.main__block-title{
  font-size: 30px;
  color: #4D4F51;
  margin-bottom: 20px;
}
.main__block-subtitle{
  font-size: 18px;
  color: #4D4F51;
}
.main__line{
  position: relative;
  width: 120px;
  border-bottom: 1px solid #4e504f;
  left: 50%;
  margin: 30px 0 10px -60px; 
  margin-bottom: 90px;
}
.main__item-block{
  width: 90%;
  height: 100%;
  position: relative;
  left: 0;
  right: 0;
  margin: auto;
}
.main__video-block-item{
  width: 45%;
  display: inline-block;
}
.main__video-space{
  width: 9%;
  display: inline-block;
}
.main__video-block-item-title{
  text-align: left;
  margin-top: 30px;
}
.veget{
  position: relative;
  width: 25%;
  display: inline-block;
  margin-bottom: 75px;
}
.veget-img{
  margin: auto;
  margin-bottom: 25px;
  position: relative;
  width: 80%;
  height: 80%;
}
.veget-name{
  font-size: 20px;
}
.goods{
  position: relative;
  width: 25%;
  display: inline-block;
  margin-bottom: 75px;
}
.goods-img{
  position: relative;
  width: 80%;
  height: 80%;
  margin: auto;
  margin-bottom: 25px;
}
.goods-name{
  font-size: 20px;
}

.header-menu{
    position: relative;
    margin-top: 75px;     
    width: 90%;
    height: 50px;
    text-align: center;
    left: 0;
    right: 0;
    margin: auto;
}
.header-menu__logo{
    position: relative;
    width: 25%;
}
.header-menu__logo-img{
    float: left;
    width: 240px;
    height: 40px;
}
.header-menu__items{
    position: relative;
    display: inline-block;
    width: 59%;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}
.header-menu__items-item{
    display: inline-block;
    width: 16.66%;
    color: #4f4f51;
    font-size: 20px;
}
.header-menu__phone{
    position: relative;
    display: inline-block;
    font-size: 20px;
    color: #4e4f51;
    text-align: right;
    width: 15%;
    right: 0;
}

.header-menu2{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 25px;
}
.header-menu2__collum{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.header-menu2__collum:nth-of-type(2){
    -webkit-box-flex: 3;
        -ms-flex: 3;
            flex: 3;
}
.header-menu{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: 15px;
}
.header-menu__item{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    letter-spacing: 0;
    color: #4D4F51;
    font-size: 18px;
}
.header-menu__logo-img{
    width: 245px;
    height: 45px;
    margin-left: 60px;
}
.header-menu2__phone{
    margin-top: 12px;
    font-size: 21px;
    color: #4f4f51;
    font-weight: medium;
}

.spinner{
    width: 100%;
    margin-top: 30px;
    margin:  0 0 30px 0;
}
.spinner__img{
    width: 100%;
    background: grey;
    height: 715px;
}
.spinner__marker-item{
    width: 10px;
    height: 10px;
    border: 2px solid #c2bcbc;
    background: white;
    border-radius: 50%;
    margin: 10px;
    display: inline-block;
}
.spinner__marker-item:hover{
    cursor: pointer;
}
.spinner__discription{
    position: absolute;
    right: 0;
    width: 40%;
    height: 675px;
    margin-top: -715px;
    background-color: rgba(255, 255, 255, 0.1);
    line-height: 1; 
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);  
    max-width: 50%;
    max-height: 50%;
    padding: 20px 40px;
}
.spinner__discription-info{
    opacity: 1;
    position: absolute;
    right: 0;
    width: 40%;
    height: 715px;
    margin-top: -715px; 
    color: white;
}
.spinner__discription-info-firstline{
    text-align: left;
    margin-left: 60px;
    font-size: 30px;
}
.spinner__discription-info-secondline{
    text-align: left;
    margin: 45px 0 60px 60px;
    font-size: 60px;
}
.spinner__discription-info-thirdline{
    text-align: left;
    margin-left: 60px;
    font-size: 15px;
}
.square{
    width: 10px;
    height: 10px;
    margin: 0 15px;
    background: white;
    display: inline-block;
}

.bottom-menu{
  width: 100%;
  height: 275px;
  background: rgb(59, 155, 59);
  background-image: url(/static/img/footer.png)
}
.bottom-menu__items{
  position: relative;
  display: inline-block;
  width: 69%;
  height: 100%;
  margin-top: 40px;
}
.bottom-menu__item{
  position: relative;
  float: left;
  top: 0;
  width: 24%;
  color: #ffffff;
  font-size: 15px;
  text-align:left;
}
